.popup {
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(254, 243, 228, 0.9);
  backdrop-filter: blur(4px);
  z-index: 100;
  overflow: auto;
  transition: 0.3s;
  padding-top: 20px;
  @media (min-width: $tablet-breakpoint) {
    padding-top: 25px;
  }
}
.popup.open {
  left: 0;
}

.open {
  @media (min-width: $desktop-breakpoint) {
    display: none;
  }
}

.menu-icon_btn--close {
  display: none;
}
.menu-open_btn .active.menu-icon_btn--close {
  display: block;
}
.menu-open_btn .active.menu-icon_btn--open {
  display: none;
}

.popup .menu {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
}

body.noscroll {
  overflow: hidden;
}

.menu-burer__link {
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: $main-text-color;
}
.logo-burger {
  text-align: center;
  display: block;
  margin-bottom: 20px;
}
.hamb {
  position: absolute;
  top: 16px;
  right: 20px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  @media (min-width: $tablet-breakpoint) {
    top: 32px;
    right: 32px;
  }
  @media (min-width: $desktop-breakpoint) {
    display: none;
  }

  transform: scale(1);
  transition: transform 250ms $animation-timing-function;
  &:hover,
  &:focus {
    color: $accent-color;
    transform: scale(1.15);
    animation-name: menuStyle;
    animation-duration: 2500ms;
    animation-timing-function: linear;
  }
}
.hamb.active {
  position: absolute;
  top: 16px;
  right: 20px;
  background-color: transparent;
  border: none;

  z-index: 999;
  @media (min-width: $tablet-breakpoint) {
    top: 25px;
    right: 67px;
  }
}

.icon-menu_close {
  display: none;
}

.hamb.active .icon-menu_close {
  display: block;
}

.hamb.active .icon-menu_open {
  display: none;
}

